import { DataSource } from '@angular/cdk/collections';
import { BehaviorSubject ,  Observable } from 'rxjs';

interface Database<T> {
    dataChange: BehaviorSubject<Array<T>>
}

export class TableDataSourceAdaptor<T> extends DataSource<T> {

    protected readonly _filterChange: BehaviorSubject<string>;

    constructor(protected readonly _database: Database<T>) {
        super();
        this._filterChange = new BehaviorSubject('');
    }

    get filter(): string { return this._filterChange.value; }
    set filter(filter: string) { this._filterChange.next(filter); }

    connect(): Observable<Array<T>> {
        return this._database.dataChange.asObservable();
    }

    disconnect(): void {
    }
}

